// Logger conditionnel - D√©sactive les logs en production
// Garde seulement console.error actif pour d√©bogage critique

const isDevelopment = window.location.hostname === 'localhost' || 
                     window.location.hostname === '127.0.0.1' ||
                     window.location.hostname === '192.168.1.1' ||
                     window.location.port === '3200'; // Port Vite

/**
 * Logger intelligent qui d√©sactive les logs en production
 * console.error reste toujours actif
 */
export const logger = {
    /**
     * Log d'information (d√©veloppement uniquement)
     */
    log: (...args) => {
        if (isDevelopment) {
            console.log(...args);
        }
    },
    
    /**
     * Log d'erreur (toujours actif)
     */
    error: (...args) => {
        console.error(...args);
    },
    
    /**
     * Log d'avertissement (d√©veloppement uniquement)
     */
    warn: (...args) => {
        if (isDevelopment) {
            console.warn(...args);
        }
    },
    
    /**
     * Log d'information (d√©veloppement uniquement)
     */
    info: (...args) => {
        if (isDevelopment) {
            console.info(...args);
        }
    },
    
    /**
     * Log de groupe (d√©veloppement uniquement)
     */
    group: (...args) => {
        if (isDevelopment) {
            console.group(...args);
        }
    },
    
    /**
     * Fin de groupe (d√©veloppement uniquement)
     */
    groupEnd: () => {
        if (isDevelopment) {
            console.groupEnd();
        }
    },
    
    /**
     * Table (d√©veloppement uniquement)
     */
    table: (...args) => {
        if (isDevelopment) {
            console.table(...args);
        }
    }
};

// Message de d√©marrage
if (isDevelopment) {
    console.log('üîß Mode d√©veloppement - Logs activ√©s');
} else {
    console.log('üöÄ Mode production - Logs d√©sactiv√©s (sauf erreurs)');
}

export default logger;
